export function toSelectOptions(arr, label = 'name', value = 'id') {
    return arr.map(item => {
        return {
            label: item[label],
            value: item[value]
        }
    });
}

export function linkToLocation(lat, long) {
    return `https://www.google.com/maps/search/?api=1&query=${lat},${long}`;
}

export function imageFromBase64(base64) {
    return `data:image/png;base64,${base64}`;
}