import React, { useState, useEffect } from 'react';
import { Col, Row, Button } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getAppUsers } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { formatDistanceToNow } from 'date-fns';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { UserActions } from './components/UserActions';
import './users.scss';
import { linkToLocation, imageFromBase64 } from '../../handlers/Utils';

const Users = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getAppUsers((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  // const handleAddUser = (newUser) => {    
  //   setData(prevUsers => [newUser, ...prevUsers]);
  // };
  
  const handleEditUser = (editedUser) => {    
    const edit = (arr, item) => {
      return arr.map(user => {        
        if(user.id === item.id) {
          user = item;
        }
        return user;
      });
    }
    setData(prevUsers => edit(prevUsers, editedUser))
  }
  
  const handleDeleteUser = (userID) => {
    setData(prevUsers => prevUsers.filter(user => {
      return user.id !== userID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <div className="mb-2">
            <Link to="/users/add"><Button color="primary">Add New User</Button></Link>
          </div>

          <ReactTable
            data={data}
            loading={isLoading}
            defaultPageSize={10}
            className="-striped -highlight bg-white"
            filterable
            defaultFilterMethod={(filter, row) =>
              !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
            columns={[
              {
                Header: "Basic",
                columns: [
                  {
                    Header: "image",
                    accessor: "image",
                    width: 75,
                    Cell: row => <Link to={`/users/${row.original.id}`}>
                      <img 
                        src={row.value ? imageFromBase64(row.value) : PROFILE_PICTURE} 
                        width={40} 
                        height={40} 
                        style={{ borderRadius: 100 }}
                        alt="user" 
                        className="d-block mx-auto"
                      />
                    </Link>,
                    filterable: false
                  },
                  {
                    Header: "First Name",
                    accessor: "firstName",
                    Cell: row => <Link to={`/users/${row.original.id}`}>
                      {row.value}
                    </Link>,
                  },
                  {
                    Header: "Last Name",
                    accessor: "lastName",
                  }
                ]
              },
              {
                Header: "Info",
                columns: [
                  {
                    Header: "phone",
                    accessor: "phone"
                  },
                  {
                    Header: "email",
                    accessor: "email"
                  },
                  {
                    Header: "country",
                    accessor: "country",
                    width: 75,
                  },
                  {
                    Header: "city",
                    accessor: "city",
                    width: 75,
                  },
                  {
                    Header: "location",
                    accessor: "latitude",
                    filterable: false,
                    width: 75,
                    Cell: row => {
                      return row.original.longitude && row.value ?
                        <Button 
                          target="_blank"
                          rel="noopener noreferrer"
                          color="info"
                          className="d-block"
                          href={linkToLocation(row.value, row.original.longitude)}
                        ><i className="cui-location-pin"></i></Button>
                      : '';
                    }
                  },
                  {
                    Header: "created at",
                    accessor: "createdAt",
                    filterable: false,
                    Cell: ({ value }) => value && formatDistanceToNow(new Date(value), { addSuffix: true })
                  },
                  {
                    Header: "actions",
                    accessor: "id",
                    filterable: false,
                    width: 150,
                    Cell: ({ original }) => (
                      <UserActions 
                        showDelete 
                        showEdit 
                        user={original} 
                        deleteCallback={handleDeleteUser}
                        editCallback={handleEditUser}
                      />
                    )
                  },
                ]
              }
            ]}
          />
        </Col>
      </Row>
    </div>
  )
}

export default Users;
